
import Countdown from 'vux/src/components/countdown/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {Register, IdentityCode} from 'api'
const phoneReg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
export default {
data () {
return {
time: null,
value: '',
start: false,
form:{
phone: '',
identicode: ''
},
showToast: false,
btnval: '发送验证码',
errVal: '请输入正确的手机号'
}
},
components: {
Countdown,
XButton,
Toast
},
methods: {
getCode(e){
if(this.start || this.form.phone == '' || !phoneReg.test(this.form.phone)){
this.showToast = true
return
}
this.start = true
this.time = 60
this.btnval = 's后重试'
let data = {
phone: this.form.phone,
token: null
}
console.log(data)
IdentityCode(data).then(res =>{
console.log(res)
})
},
onBlur(e){

},
finish (index) {
this.start = false
this.time = null
this.btnval = '发送验证码'
},
handleSubmit(e){
if(this.form.phone == '' || this.form.identicode == '' || !phoneReg.test(this.form.phone)){
return
}
let data = {
phone: this.form.phone,
code: this.form.identicode,
token: null
}
console.log(data)
Register(data).then(res =>{
console.log(res)
})
}
},
mounted () {

}
}



